import {VanillaJsFileNames} from './Common';
interface IndexHtmlOptions {
	fileNames: VanillaJsFileNames;
}

export const indexhtml = (options: IndexHtmlOptions) => {
	const {fileNames} = options;
	return `<!DOCTYPE html>
<html>
	<head>
		<title>Polygonjs Export</title>
		<meta charset="UTF-8" />
		<link rel="stylesheet" media="screen" href="${fileNames.css.globals}" />
		<link rel="stylesheet" media="screen" href="${fileNames.css.viewSceneInPolygonjs}" />
	</head>

	<body>
	
		<div id="app"></div>

		<div id="infos-container" onclick=''>
			<img src='./polygonjs.png'/>
			<span id='infos-label'>Open in <span id='infos-highlighted'>Polygonjs Editor</span></span>
		</div>

		<script src="${fileNames.js.index}"></script>
		<script src="${fileNames.js.viewSceneInPolygonjs}"></script>
	</body>
</html>
	`;
};
