import {StoreController} from './../../store/controllers/StoreController';
import {BackgroundMode} from './../../../../@polygonjs/polygonjs/src/engine/nodes/manager/utils/Scene/Background';
import {TypeAssert} from './../../../../@polygonjs/polygonjs/src/engine/poly/Assert';
import {CoreString} from './../../../../@polygonjs/polygonjs/src/core/String';
import {MetaSceneTraverser} from './../../core/scene_traversers/Meta';
import {PolyScene} from './../../../../@polygonjs/polygonjs/src/engine/scene/PolyScene';
import {FilesDataOptions, PackageJSONOptions} from './Common';
import {versions, sceneDataWithVersion} from '../../versions';
import {vanillaJSFilesData} from './vanillajs/filesData';
import {reactJSFilesData} from './reactjs/filesData';
import {reactThreeFiberJSFilesData} from './reactthreefiber/filesData';
import {threeJSFilesData} from './threejs/filesData';
import {vueJSFilesData} from './vuejs/filesData';
import {BaseSaveController} from './../../core/SceneSaveController';
import {arrayToSet} from './../../../../@polygonjs/polygonjs/src/core/ArrayUtils';

const API_URL = 'https://codesandbox.io/api/v1/sandboxes/define?json=1';

export enum CodeSandboxExportType {
	VANILLAJS = 'Vanilla JS',
	THREEJS = 'Three.js',
	REACTJS = 'React',
	REACT_THREE_FIBER = 'React Three Fiber',
	VUEJS = 'Vue',
}

const PACKAGEJSON_OPTIONS: PackageJSONOptions = {
	projectName: 'tmp',
	description: 'Polygonjs Export',
	keywords: ['webgl', 'threejs', 'polygonjs'],
	versions: {
		polygonjs: versions().polygonjs,
		polygonjsEditor: versions().editor,
		threejs: '0.148.0',
		threejsTypes: '0.148.0',
		plugins: {
			react: '^1.2.30',
			r3f: '^1.2.89',
			vue: '^1.2.67-2',
		},
	},
};

export class CodeSandboxExporter {
	constructor(public readonly scene: PolyScene) {}

	async createSandbox(type: CodeSandboxExportType) {
		const code = await sceneDataWithVersion({scene: this.scene, withPersistedConfig: true});
		const sceneName = this.scene.name() || 'unnamed_scene';
		// traverse scene
		const metaTraverser = new MetaSceneTraverser(this.scene);
		await metaTraverser.traverseScene();

		const nodes = metaTraverser.nodes.usedNodes();
		const polyNodes = metaTraverser.polyNodes.usedPolyNodes();
		const operations = metaTraverser.nodes.usedOperations();
		const jsFunctionTypes = metaTraverser.nodes.usedJsFunctionTypes();
		// const nodes_map = metaTraverser.nodesMap.nodes_map();
		// const poly_nodes = metaTraverser.polyNodes.usedPolyNodes();

		// register nodes
		console.log(polyNodes);
		const polyNodesSet: Set<string> = new Set();
		arrayToSet(polyNodes, polyNodesSet);
		const importLines: string[] = [];
		const registerLines: string[] = [];
		for (const node of nodes) {
			if (!polyNodesSet.has(node)) {
				const [nodeContext, nodeType] = node.split('/');
				const fileName = `${CoreString.titleize(nodeType)}`;
				const nodeClass = `${CoreString.titleize(nodeType)}${CoreString.titleize(nodeContext)}Node`;
				importLines.push(
					`import { ${nodeClass} } from '@polygonjs/polygonjs/dist/src/engine/nodes/${nodeContext.toLowerCase()}/${fileName}';`
				);
				registerLines.push(`Poly.registerNode(${nodeClass});`);
			}
		}
		// register operations
		for (const operation of operations) {
			const [operationContext, operationType] = operation.split('/');
			const fileName = `${CoreString.titleize(operationType)}`;
			const operationClass = `${CoreString.titleize(operationType)}${CoreString.titleize(
				operationContext
			)}Operation`;
			importLines.push(
				`import { ${operationClass} } from '@polygonjs/polygonjs/dist/src/engine/operations/${operationContext.toLowerCase()}/${fileName}';`
			);
			registerLines.push(`Poly.registerOperation(${operationClass});`);
		}
		// register jsFunctionTypes
		for (const jsFunctionType of jsFunctionTypes) {
			const fileName = jsFunctionType;
			const functionClass = jsFunctionType;
			importLines.push(
				`import { ${functionClass} } from '@polygonjs/polygonjs/dist/src/engine/functions/${fileName}';`
			);
			registerLines.push(`Poly.registerNamedFunction(${functionClass});`);
		}

		const bgColor =
			this.scene.root().sceneBackgroundController.backgroundMode() == BackgroundMode.COLOR
				? this.scene.root().p.bgColor.value
				: undefined;

		BaseSaveController.embedPolyNodes(code);
		const options: FilesDataOptions = {
			bgColor: bgColor,
			sceneName,
			code,
			importLines,
			registerLines,
			packageJsonOptions: PACKAGEJSON_OPTIONS,
		};

		switch (type) {
			case CodeSandboxExportType.VANILLAJS: {
				return this._submitFilesData(vanillaJSFilesData(options));
			}
			case CodeSandboxExportType.THREEJS: {
				return this._submitFilesData(threeJSFilesData(options));
			}
			case CodeSandboxExportType.REACTJS: {
				return this._submitFilesData(reactJSFilesData(options));
			}
			case CodeSandboxExportType.REACT_THREE_FIBER: {
				return this._submitFilesData(reactThreeFiberJSFilesData(options));
			}
			case CodeSandboxExportType.VUEJS: {
				return this._submitFilesData(vueJSFilesData(options));
			}
		}
		TypeAssert.unreachable(type);
	}

	private async _submitFilesData(filesData: object): Promise<{sandboxUrl: string} | undefined> {
		const body = JSON.stringify(filesData);

		const errorMessage = `It seems we can't connect to codesandbox, maybe try again`;

		try {
			const controller = new AbortController();
			const timeoutDuration = 10;
			const timeoutId = setTimeout(() => {
				controller.abort();
				StoreController.editor.status.setError(errorMessage);
			}, timeoutDuration * 1000);
			const response = await fetch(API_URL, {
				signal: controller.signal,
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					Accept: 'application/json',
				},
				body: body,
			});
			if (response.ok) {
				const json = await response.json();
				clearTimeout(timeoutId);
				const sandbox_id = json['sandbox_id'];
				if (sandbox_id) {
					const sandboxUrl = `https://codesandbox.io/s/${sandbox_id}`;

					return {sandboxUrl};
				} else {
					StoreController.editor.status.setError(errorMessage);
				}
			}
		} catch (err) {
			StoreController.editor.status.setError(errorMessage);
		}
	}
}
