import {FunctionNames} from '../../common/FunctionNames';
interface IndexJsOptions {
	functionNames: FunctionNames;
}

export const indexjs = (options: IndexJsOptions) => {
	const {functionNames} = options;

	return `import {${functionNames.createAndMountScene}} from './createAndMountScene';

const onProgress = (progress)=>{
	console.log(\`progress: \${progress}\`);
}

${functionNames.createAndMountScene}({
	domElement: 'app',
	onProgress,
	autoPlay: true,
	createViewer: true,
});

	`;
};
