import {FunctionNames} from '../FunctionNames';
interface CreateViewerOptions {
	importLines: string[];
	registerLines: string[];
	functionNames: FunctionNames;
}

export const createAndMountScenejs = (options: CreateViewerOptions) => {
	const {importLines, registerLines, functionNames} = options;
	const functionName: string = functionNames.createAndMountScene;

	return `import {${functionNames.sceneData}} from './sceneData';
import { ScenePlayerImporter } from '@polygonjs/polygonjs/dist/src/engine/io/player/Scene';
import { Poly } from '@polygonjs/polygonjs/dist/src/engine/Poly';
import { AllCamerasRegister } from '@polygonjs/polygonjs/dist/src/engine/poly/registers/cameras/All';
import { AllExpressionsRegister } from '@polygonjs/polygonjs/dist/src/engine/poly/registers/expressions/All';
${importLines.join('\n')}

// register nodes & operations
${registerLines.join('\n')}

// register camera types and expressions
AllCamerasRegister.run(Poly);
AllExpressionsRegister.run(Poly);

// create and mount scene
const sceneData = ${functionNames.sceneData}();
export async function ${functionName}(options){
	const { scene, viewer } = await ScenePlayerImporter.loadSceneData({...options, sceneData});
	return { viewer, scene };
}`;
};
