import {viewSceneInPolygonjs_css} from './../../common/css/viewSceneInPolygonjs_css';
import {filePathShortWithoutExtension} from '../../Common';
import {FunctionNames} from '../../common/FunctionNames';
import {FILE_NAMES} from '../Common';
interface ViewInPolygonjsOptions {
	functionNames: FunctionNames;
}

viewSceneInPolygonjs_css;

export const viewInPolygonjs_vue = (options: ViewInPolygonjsOptions) => {
	const {functionNames} = options;
	const functionName: string = functionNames.viewSceneInPolygonjs;

	const jsFileName = filePathShortWithoutExtension(FILE_NAMES.js.viewInPolygonjs);

	return `
<template>
	<div id="infos-container" @click="onClick">
		<img src="../../polygonjs.png" alt="Polygonjs Icon" />
		<span id="infos-label">
			Open in <span id="infos-highlighted">Polygonjs Editor</span>
		</span>
	</div>
</template>

<script>
import {defineComponent} from 'vue';
import {${functionName}} from '../${jsFileName.fileName}'
export default defineComponent({
	name: 'ExportPanel',
	components: {},
	setup() {
		
		function onClick(){
			${functionName}();
		}

		return {
			onClick,
		};
	}
})
</script>

<style>
${viewSceneInPolygonjs_css()}
</style>`;
};
