import {PackageJSONOptions} from './../Common';
export const vanillaJsPackagejson = (options: PackageJSONOptions) => {
	const {projectName, description, keywords, versions} = options;

	return {
		name: projectName,
		version: '1.0.0',
		description,
		main: 'index.html',
		scripts: {
			start: 'parcel index.html --open',
			build: 'parcel build index.html',
		},
		dependencies: {
			'@polygonjs/polygonjs': `${versions.polygonjs}`,
			'parcel-bundler': '^1.6.1',
		},
		devDependencies: {
			'@babel/core': '7.2.0',
			typescript: '4.4.4',
		},
		resolutions: {
			'@babel/preset-env': '7.13.8',
		},
		keywords,
	};
};
