import {CoreString} from '../../../../../@polygonjs/polygonjs/src/core/String';

export interface FunctionNames {
	createAndMountScene: string;
	sceneData: string;
	viewSceneInPolygonjs: string;
}

interface GetFunctionNamesOptions {
	sceneName: string;
}
export function getFunctionNames(options: GetFunctionNamesOptions) {
	const {sceneName} = options;
	const sceneDataFunctionName = `sceneData_${sceneName}`;
	const functionNames: FunctionNames = {
		createAndMountScene: `createAndMountScene_${sceneName}`,
		sceneData: sceneDataFunctionName,
		viewSceneInPolygonjs: `viewSceneInPolygonjs_${sceneName}`,
	};

	return {sceneDataFunctionName, functionNames};
}

export function sanitizeProjectName(projectName: string) {
	projectName = projectName.toLowerCase().replace(/ /g, '-');
	return CoreString.sanitizeName(projectName);
}
