import {FunctionNames} from '../FunctionNames';
interface OpenSceneInPolygonjsOptions {
	sceneName: string;
	functionNames: FunctionNames;
}

const API_URL = process.env.NODE_ENV == 'development' ? 'http://localhost:5000' : 'https://polygonjs.com';

export const viewSceneInPolygonjsjs = (options: OpenSceneInPolygonjsOptions) => {
	const {sceneName, functionNames} = options;
	const functionName: string = functionNames.viewSceneInPolygonjs;

	return `import {${functionNames.sceneData}} from './sceneData';

export function ${functionName}(){
	const sceneData = ${functionNames.sceneData}();

	const encoded = encodeURI(JSON.stringify(sceneData));

	const formHTML = \`<form action="${API_URL}/demo" method="POST" target="_blank">
		<input type="hidden" name="scene[name]" value="${sceneName}" />
		<input type="hidden" name="scene[code]" value="\${encoded}" />
		<input type="submit" value="Open in polygonjs" />
	</form>\`;
	console.log(formHTML);
	
	const element = document.createElement('div');
	document.body.appendChild(element);
	element.innerHTML = formHTML;
	const form = element.querySelector('form');
	form.submit();
	// document.body.removeChild(element);
}

let _eventAdded = false;
function addEvent(){
	if(_eventAdded){
		return
	}
	const domElement = document.getElementById('infos-container');
	if(!domElement){
		return
	}
	domElement.addEventListener('click', ${functionName});
	_eventAdded = true;
}
document.addEventListener('DOMContentLoaded',addEvent);
addEvent();
`;
};
