import {filePathShortWithoutExtension} from '../../Common';
import {FunctionNames} from '../../common/FunctionNames';
import {FILE_NAMES} from '../Common';

// import {ReactJsFileNames} from '../Common';
interface IndexHtmlOptions {
	sceneName: string;
	functionNames: FunctionNames;
}

export const index_js = (options: IndexHtmlOptions) => {
	const jsxFileName = filePathShortWithoutExtension(FILE_NAMES.js.viewInPolygonjsx);

	return `
import './index.css';
import { createRoot } from 'react-dom/client'
import React, { useRef, useState } from 'react'
import {App} from "./App";
import {ViewInPolygonjs} from './components/${jsxFileName.fileName}';

const domElement = document.getElementById('root');
createRoot(domElement).render(
	<div id='app-container'>
		<App />
		<h1>R3F + Polygonjs</h1>
		<ViewInPolygonjs />
	</div>
	,
)
	
  `;
};
