import {viewInPolygonjs_vue} from '././js/ViewInPolygonjs_vue';
import {createAndMountScenejs} from './../common/js/createAndMountScene_js';
import {reactJsPackagejson} from './package_json';
import {FilesDataOptions, POLYGONJS_ICON_URL} from './../Common';
import {indexhtml} from './index_html';
// import {infoContainer_css} from '../vanillajs/css/infoContainer_css';
import {mainjs} from './js/main_js';
import {app_vue} from './js/App_vue';
import {sceneDatajs} from '../common/js/sceneData_js';
import {viewSceneInPolygonjsjs} from '../common/js/viewSceneInPolygonjs';
// import {createAndMountScenejs} from './js/createAndMountScene_js';
// import {viewSceneInPolygonjsjs} from './js/viewSceneInPolygonjs';
import {FILE_NAMES} from './Common';
import {getFunctionNames, sanitizeProjectName} from '../common/FunctionNames';

export function vueJSFilesData(options: FilesDataOptions) {
	const {sceneName, code, importLines, registerLines, packageJsonOptions} = options;

	packageJsonOptions.projectName = sanitizeProjectName('Polygonjs Export to Vuejs');

	const {sceneDataFunctionName, functionNames} = getFunctionNames({sceneName});

	return {
		files: {
			'package.json': {
				isBinary: false,
				content: reactJsPackagejson(packageJsonOptions),
			},
			'polygonjs.png': {
				isBinary: true,
				content: POLYGONJS_ICON_URL,
			},
			[FILE_NAMES.html.index]: {
				isBinary: false,
				content: indexhtml({}),
			},
			// CSS
			// [FILE_NAMES.css.styles]: {
			// 	isBinary: false,
			// 	content: styles_css({}),
			// },
			// [FILE_NAMES.css.infosContainer]: {
			// 	isBinary: false,
			// 	content: infoContainer_css(),
			// },
			// JS
			[FILE_NAMES.js.main]: {
				isBinary: false,
				content: mainjs({functionNames}),
			},
			[FILE_NAMES.js.app]: {
				isBinary: false,
				content: app_vue({sceneName, functionNames}),
			},
			[FILE_NAMES.js.loadScene]: {
				isBinary: false,
				content: createAndMountScenejs({importLines, registerLines, functionNames}),
			},
			'src/sceneData.js': {
				isBinary: false,
				content: sceneDatajs({code, functionName: sceneDataFunctionName}),
			},
			// viewInPolygonjs
			[FILE_NAMES.js.viewInPolygonjs]: {
				isBinary: false,
				content: viewSceneInPolygonjsjs({sceneName, functionNames}),
			},
			[FILE_NAMES.js.viewInPolygonvue]: {
				isBinary: false,
				content: viewInPolygonjs_vue({functionNames}),
			},
			// 'src/createAndMountScene.js': {
			// 	isBinary: false,
			// 	content: createAndMountScenejs({importLines, registerLines, functionNames}),
			// },
			// [FILE_NAMES.js.viewSceneInPolygonjs]: {
			// 	isBinary: false,
			// 	content: viewSceneInPolygonjsjs({sceneName, functionNames}),
			// },
		},
	};
}
