import {PackageJSONOptions} from './../Common';

export const reactJsPackagejson = (options: PackageJSONOptions) => {
	const {projectName, description, keywords, versions} = options;

	return {
		name: projectName,
		version: '1.0.0',
		description,
		keywords,
		main: 'src/index.js',
		dependencies: {
			'@polygonjs/polygonjs': `${versions.polygonjs}`,
			'@polygonjs/react': `${versions.plugins.react}`,
			react: '18.2.0',
			'react-dom': '18.2.0',
			'react-scripts': '4.0.0',
		},
		devDependencies: {
			'@babel/runtime': '7.13.8',
			typescript: '4.1.3',
		},
		scripts: {
			start: 'react-scripts start',
			build: 'react-scripts build',
			test: 'react-scripts test --env=jsdom',
			eject: 'react-scripts eject',
		},
		browserslist: ['>0.2%', 'not dead', 'not ie <= 11', 'not op_mini all'],
	};
};
