export function filePathShortWithoutExtension(filePath: string) {
	const elements = filePath.split('/');
	const fileName = elements[elements.length - 1];
	const fileNameElements = fileName.split('.');
	const fileNameNoExt = fileNameElements[0];
	return {fileName, fileNameNoExt};
}
