import {FunctionNames} from '../../common/FunctionNames';
interface IndexJsOptions {
	functionNames: FunctionNames;
}

export const indexjs = (options: IndexJsOptions) => {
	const {functionNames} = options;

	return `import {${functionNames.createAndMountScene}} from './createAndMountScene';
import * as THREE from 'three'

// scene
const scene = new THREE.Scene();

// camera
const camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );
camera.position.set(5,5,5);
camera.lookAt(0,0,0);

// renderer
const app = document.getElementById('app');
const canvas = app.querySelector('canvas');
const renderer = new THREE.WebGLRenderer({canvas});
renderer.setSize( window.innerWidth, window.innerHeight );

// cube
const geometry = new THREE.BoxGeometry();
const material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
const cube = new THREE.Mesh( geometry, material );
scene.add( cube );

// polygonjs scene
const onProgress = (progress)=>{
	console.log(\`progress: \${progress}\`);
}

let polygonjsScene;
${functionNames.createAndMountScene}({
	onProgress,
	createViewer: false,
}).then((loadedData)=>{
	polygonjsScene = loadedData.scene;
	// Polygonjs particles need a renderer in order to be simulated.
	// When you use Polygonjs as a standalone, this is taken care of automatically.
	// But if you integrate it with threejs, you need to give it the renderer that will be used. 
	polygonjsScene.registerRenderer(renderer);
	// here we add polygonjs scene to your main scene.
	scene.add(polygonjsScene.threejsScene());

	animate();
})

const clock = new THREE.Clock();
function animate() {
	const delta = clock.getDelta();
	requestAnimationFrame( animate );
	
	// when using your own renderer,
	// you need to run \`.update(delta)\` on polygonjs scene
	// to allow it to track the current time and update its internals  
	polygonjsScene.update(delta);

	cube.rotation.x += 0.01;
	cube.rotation.y += 0.01;

	renderer.render( scene, camera );
};

`;
};
