import type {Color} from 'three';
interface AppCssOptions {
	bgColor?: Color;
}

export const App_css = (options: AppCssOptions) => {
	const {bgColor} = options;
	const lines: string[] = [];
	if (bgColor) {
		lines.push(`canvas {
	background-color: ${bgColor.getStyle()};
}`);
	}
	return lines.join('\n');
};
