// import {filePathShortWithoutExtension} from '../../Common';
import {filePathShortWithoutExtension} from '../../Common';
import {FunctionNames} from '../../common/FunctionNames';
import {FILE_NAMES} from '../Common';
// import {FILE_NAMES} from '../Common';
interface IndexHtmlOptions {
	sceneName: string;
	functionNames: FunctionNames;
}

export const app_vue = (options: IndexHtmlOptions) => {
	const {sceneName, functionNames} = options;

	const vueFileName = filePathShortWithoutExtension(FILE_NAMES.js.viewInPolygonvue);

	return `<template>
	<PolygonjsScene
		:loadFunction="loadFunction"
		:sceneName="'${sceneName}'"
		@progress='onProgress'
		@sceneready='onSceneReady'
	/>
	<h1>Polygonjs + Vuejs</h1>
	<ViewInPolygonjs />
</template>

<script>
import {defineComponent, computed} from 'vue';
import { PolygonjsScene } from "@polygonjs/vue3";
import {${functionNames.createAndMountScene}} from '../${FILE_NAMES.js.loadScene}';
import ViewInPolygonjs from './components/${vueFileName.fileName}'
export default defineComponent({
	name: 'App',
	components: {
		PolygonjsScene,
		ViewInPolygonjs
	},
	setup() {
		const loadFunction = computed(()=>${functionNames.createAndMountScene})

		function onProgress(progress){
			console.log(\`progress:\${progress}\`);
		}
		function onSceneReady(scene){
			console.log('scene ready');
		}

		return {
			loadFunction,
			onProgress,
			onSceneReady
		};
	}
})
</script>

<style>
h1 {
	position: absolute;
	top: 0px;
	width: 100%;
	text-align: center;
	pointer-events: none;
}
</style>`;
};
