import {FunctionNames} from '../../common/FunctionNames';
import {FILE_NAMES} from '../Common';

interface IndexHtmlOptions {
	sceneName: string;
	functionNames: FunctionNames;
}

export const App_js = (options: IndexHtmlOptions) => {
	const {sceneName, functionNames} = options;

	return `
import './App.css';
import React, { useRef, useState } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'


import {PolygonjsScene} from '@polygonjs/react-three-fiber';
import {${functionNames.createAndMountScene}} from '../${FILE_NAMES.js.loadScene}';

function Box(props) {
	// This reference will give us direct access to the mesh
	const mesh = useRef()
	// Set up state for the hovered and active state
	const [hovered, setHover] = useState(false)
	const [active, setActive] = useState(false)
	// Subscribe this component to the render-loop, rotate the mesh every frame
	useFrame((state, delta) => (mesh.current.rotation.x += 0.01))
	// Return view, these are regular three.js elements expressed in JSX
	return (
	<mesh
		{...props}
		ref={mesh}
		scale={active ? 1.5 : 1}
		onClick={(event) => setActive(!active)}
		onPointerOver={(event) => setHover(true)}
		onPointerOut={(event) => setHover(false)}>
		<boxGeometry args={[1, 1, 1]} />
		<meshStandardMaterial color={hovered ? 'hotpink' : 'orange'} />
	</mesh>
	)
}

export function App() {

	return (
		<Canvas legacy={true}>
			<OrbitControls makeDefault />
			<ambientLight />
			<pointLight position={[10, 10, 10]} />
			<Box position={[-1.2, 0, 0]} />
			<Box position={[1.2, 0, 0]} />
			<PolygonjsScene
				sceneName={'${sceneName}'}
				loadFunction={${functionNames.createAndMountScene}}
			/>
		</Canvas>
)}
	
  `;
};
