import {filePathShortWithoutExtension} from '../../Common';
import {FunctionNames} from '../../common/FunctionNames';
import {FILE_NAMES} from '../Common';
interface IndexHtmlOptions {
	sceneName: string;
	functionNames: FunctionNames;
}

export const appjs = (options: IndexHtmlOptions) => {
	const {sceneName, functionNames} = options;

	const jsxFileName = filePathShortWithoutExtension(FILE_NAMES.js.viewInPolygonjsx);

	return `import "./styles.css";

import {ViewInPolygonjs} from './components/${jsxFileName.fileName}';
import {PolygonjsScene} from '@polygonjs/react';
import {${functionNames.createAndMountScene}} from '../${FILE_NAMES.js.loadScene}';


function onSceneReady(scene) {
	console.log('onSceneReady', scene.name());
}

export default function App() {
	return (
	<div className="App">
		<PolygonjsScene
			key="${sceneName}"
			sceneName="${sceneName}"
			loadFunction={${functionNames.createAndMountScene}}
			onSceneReady={onSceneReady}
			render={true}
		/>
		<h1>React + Polygonjs</h1>
		<ViewInPolygonjs />
	</div>
	);
}
	`;
};
