import {viewInPolygonjs_jsx} from '../reactjs/js/ViewInPolygonjs_jsx';
import {viewSceneInPolygonjs_css} from './../common/css/viewSceneInPolygonjs_css';
import {createAndMountScenejs} from './../common/js/createAndMountScene_js';
import {reactThreeFiberPackagejson} from './package_json';
// import {reactThreeFiberTsConfig} from './tsconfig_json';
import {FilesDataOptions, POLYGONJS_ICON_URL} from './../Common';
import {indexhtml} from './index_html';
import {index_css} from './css/index_css';
import {App_css} from './css/App_css';
// import {infoContainer_css} from '../vanillajs/css/infoContainer_css';
import {index_js} from './js/index_js';
import {App_js} from './js/App_js';
import {sceneDatajs} from '../common/js/sceneData_js';
import {viewSceneInPolygonjsjs} from '../common/js/viewSceneInPolygonjs';
// import {createAndMountScenejs} from './js/createAndMountScene_js';
// import {viewSceneInPolygonjsjs} from './js/viewSceneInPolygonjs';
import {FILE_NAMES} from './Common';
import {getFunctionNames, sanitizeProjectName} from '../common/FunctionNames';

export function reactThreeFiberJSFilesData(options: FilesDataOptions) {
	const {bgColor, sceneName, code, importLines, registerLines, packageJsonOptions} = options;

	packageJsonOptions.projectName = sanitizeProjectName('Polygonjs Export to React Three Fiber');

	const {sceneDataFunctionName, functionNames} = getFunctionNames({sceneName});

	return {
		files: {
			'package.json': {
				isBinary: false,
				content: reactThreeFiberPackagejson(packageJsonOptions),
			},
			'polygonjs.png': {
				isBinary: true,
				content: POLYGONJS_ICON_URL,
			},
			// 'tsconfig.json': {
			// 	isBinary: true,
			// 	content: reactThreeFiberTsConfig(packageJsonOptions),
			// },
			[FILE_NAMES.html.index]: {
				isBinary: false,
				content: indexhtml({}),
			},
			// CSS
			[FILE_NAMES.css.index]: {
				isBinary: false,
				content: index_css({}),
			},
			[FILE_NAMES.css.app]: {
				isBinary: false,
				content: App_css({bgColor}),
			},
			// [FILE_NAMES.css.infosContainer]: {
			// 	isBinary: false,
			// 	content: infoContainer_css(),
			// },
			// JS
			[FILE_NAMES.js.index]: {
				isBinary: false,
				content: index_js({sceneName, functionNames}),
			},
			[FILE_NAMES.js.app]: {
				isBinary: false,
				content: App_js({sceneName, functionNames}),
			},
			[FILE_NAMES.js.loadScene]: {
				isBinary: false,
				content: createAndMountScenejs({importLines, registerLines, functionNames}),
			},
			'src/sceneData.js': {
				isBinary: false,
				content: sceneDatajs({code, functionName: sceneDataFunctionName}),
			},
			// viewInPolygonjs
			[FILE_NAMES.js.viewInPolygonjs]: {
				isBinary: false,
				content: viewSceneInPolygonjsjs({sceneName, functionNames}),
			},
			[FILE_NAMES.js.viewInPolygonjsx]: {
				isBinary: false,
				content: viewInPolygonjs_jsx({functionNames}),
			},
			[FILE_NAMES.css.viewInPolygonjs]: {
				isBinary: false,
				content: viewSceneInPolygonjs_css(),
			},
			// 'src/createAndMountScene.js': {
			// 	isBinary: false,
			// 	content: createAndMountScenejs({importLines, registerLines, functionNames}),
			// },
			// [FILE_NAMES.js.viewSceneInPolygonjs]: {
			// 	isBinary: false,
			// 	content: viewSceneInPolygonjsjs({sceneName, functionNames}),
			// },
		},
	};
}
